.container, .library-container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 1fr);
  max-width: 768px;
  place-items: center;
  row-gap: 20px;
  margin-inline: auto;
  padding: 20px 0 40px;
  display: grid;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container, .library-container {
    padding: 20px 15px 40px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container, .library-container {
    --column-count: 2;
    gap: 32px;
  }
}

@media screen and (min-width: 768px) {
  .container, .library-container {
    place-items: start;
    padding: 60px 23px;
  }
}

@media screen and (min-width: 1280px) {
  .container, .library-container {
    --column-count: 3;
    max-width: 1280px;
    gap: 32px 16px;
  }
}

.wrapper, .wrapper.library {
  height: 100vh;
  height: 100dvh;
  grid-template-rows: auto 1fr 1fr auto;
  grid-template-areas: "header"
                       "main"
                       "pagination"
                       "footer";
  display: grid;
}

.wrapper .header, .wrapper .header-library {
  grid-area: header;
}

.wrapper main, .wrapper.library main {
  grid-area: main;
}

.wrapper .pagination {
  grid-area: pagination;
}

.wrapper footer, .wrapper.library footer, .wrapper:nth-child(5) {
  grid-area: footer;
}

.wrapper.library {
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header"
                       "main"
                       "footer";
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
}

.link {
  text-decoration: none;
}

.uppercase {
  text-transform: uppercase;
}

[aria-current="page"] {
  border-bottom: 3px solid red;
}

:root {
  --FF: Roboto, sans-serif;
  --fs-800: 1.875rem;
  --fs-700: 1.25rem;
  --fs-600: 1rem;
  --fs-500: .875rem;
  --fs-400: .75rem;
  --fs-card-info: clamp(.75rem, calc(0rem + 1.56vw), 1.25rem);
  --fs-movie-modal-title: clamp(1.25rem, calc(.31rem + 1.95vw), 1.88rem);
  --fs-copyright: clamp(.88rem, calc(.79rem + .45vw), 1rem);
  --fw-500: 500;
  --fw-400: 400;
  --lh-600: 1.667;
  --lh-500: 1.34;
  --lh-400: 1.167;
  --lh-300: 1.143;
  --lh-copyright: 1.188;
}

.container-arrow-up {
  width: 47px;
  height: 47px;
  border-radius: 50%;
  line-height: 0;
  position: fixed;
  bottom: 49px;
  right: 19px;
}

.container-arrow-up:hover .arrow {
  fill: #ff6b08;
  border: 3px solid #ff6b08;
  animation: 1s infinite arrowFloat;
}

.container-arrow-up:hover .arrow .arrow__icon {
  fill: #ff6b08;
}

@media screen and (min-width: 768px) {
  .container-arrow-up {
    bottom: 19px;
  }
}

.arrow {
  width: 45px;
  height: 45px;
  color: #545454;
  opacity: .75;
  cursor: pointer;
  border: 3px solid #bababa;
  border-radius: 50%;
  line-height: 0;
  position: fixed;
  bottom: 50px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .arrow {
    bottom: 20px;
  }
}

@keyframes arrowFloat {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(4px);
  }

  100% {
    transform: translateY(0);
  }
}

.arrow__icon {
  fill: #bababa;
  opacity: .75;
  transform: translateX(-1px);
}

