#modal-container {
  height: 100%;
  width: 100%;
  z-index: 999;
  display: table;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(0);
}

#modal-container.visible {
  animation: 1s cubic-bezier(.165, .84, .44, 1) forwards unfoldIn;
  transform: scaleY(.01)scaleX(0);
}

#modal-container.visible .modal-backdrop .modal {
  background-color: #86868617;
  border: 2px solid #ffffff4d;
  border-radius: 24px;
  animation: .5s cubic-bezier(.165, .84, .44, 1) .8s forwards zoomIn;
  transform: scale(0);
}

#modal-container.hidden {
  pointer-events: none;
  animation: 1s cubic-bezier(.165, .84, .44, 1) .3s forwards unfoldOut;
  transform: scale(1);
}

#modal-container.hidden .modal-backdrop .modal {
  animation: .5s cubic-bezier(.165, .84, .44, 1) forwards zoomOut;
}

#modal-container .modal-backdrop {
  text-align: center;
  vertical-align: middle;
  background: #000c;
  padding: 30px;
  display: table-cell;
}

#modal-container .modal-backdrop .modal {
  max-width: 1300px;
  max-height: 90vh;
  background-image: radial-gradient(at 49% 49%, #691a07 0, #0000 40%), radial-gradient(at 99% 44%, #ff000075 0, #0000 40%), radial-gradient(at 100% 0, #d91402 0, #0000 40%), radial-gradient(at 62% 100%, #691a07 0, #0000 60%), radial-gradient(at 1% 100%, #8e1a06 0, #0000 15%), radial-gradient(at 7% 69%, #6b210a 0, #0000 30%), radial-gradient(at 0 0, #da3f07 0, #0000 40%);
  border-radius: 3px;
